<template>
  <div class="pos">
    <el-row type="flex">
      <el-col :span="7" class="pos-order" id="order-list">
        <el-tabs type="card">
          <el-tab-pane label="点餐">
            <el-table :data="tableData" border style="width:100%">
              <el-table-column
                prop="goodsName"
                label="商品名称"
              ></el-table-column>
              <el-table-column prop="count" label="数量"></el-table-column>
              <el-table-column prop="price" label="金额"></el-table-column>
              <el-table-column label="操作" fixed="right" width="100">
                <template slot-scope="scope">
                  <el-button
                    type="text"
                    size="mini"
                    @click="deleteGoods(scope.row)"
                    >删除</el-button
                  >
                  <el-button
                    type="text"
                    size="mini"
                    @click="addOrderList(scope.row)"
                    >增加</el-button
                  >
                </template>
              </el-table-column>
            </el-table>
            <div class="total">
              <span>数量：{{ totalNum }}</span>
              <span>金额：{{ totalPrice }}元</span>
            </div>
            <div class="btns">
              <el-button type="warning">挂单</el-button>
              <el-button type="danger" @click="deleteAll">删除</el-button>
              <el-button type="success" @click="checkOut">结账</el-button>
            </div>
          </el-tab-pane>
          <el-tab-pane label="挂单">
            挂单
          </el-tab-pane>
          <el-tab-pane label="外卖">
            外卖
          </el-tab-pane>
        </el-tabs>
      </el-col>
      <el-col :span="17" style="backgroundColor:#EFF3F8">
        <div class="often-goods">
          <div class="title">常用商品</div>
          <div
            class="often-goods-list"
            unselectable="on"
            style="-moz-user-select:none;"
            onselectstart="return false;"
          >
            <ul>
              <li
                v-for="item in oftenGoods"
                :key="item.goodsId"
                @click="addOrderList(item)"
              >
                <span>{{ item.goodsName }}</span>
                <span class="o-price">￥{{ item.price }}</span>
              </li>
            </ul>
          </div>
        </div>
        <div class="goods-type">
          <el-tabs>
            <el-tab-pane label="汉堡">
              <div
                unselectable="on"
                style="-moz-user-select:none;"
                onselectstart="return false;"
              >
                <ul class="cookList">
                  <li
                    v-for="goods in type0Goods"
                    :key="goods.goodsId"
                    @click="addOrderList(goods)"
                  >
                    <span class="foodImg"
                      ><img :src="goods.goodsImg" width="100%" height="60px"
                    /></span>
                    <span class="foodName">{{ goods.goodsName }}</span>
                    <span class="foodPrice">￥{{ goods.price }}元</span>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
            <el-tab-pane label="小食">
              <div
                unselectable="on"
                style="-moz-user-select:none;"
                onselectstart="return false;"
              >
                <ul class="cookList">
                  <li
                    v-for="goods in type1Goods"
                    :key="goods.goodsId"
                    @click="addOrderList(goods)"
                  >
                    <span class="foodImg"
                      ><img :src="goods.goodsImg" width="100%" height="60px"
                    /></span>
                    <span class="foodName">{{ goods.goodsName }}</span>
                    <span class="foodPrice">￥{{ goods.price }}元</span>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
            <el-tab-pane label="饮料">
              <div
                unselectable="on"
                style="-moz-user-select:none;"
                onselectstart="return false;"
              >
                <ul class="cookList">
                  <li
                    v-for="goods in type2Goods"
                    :key="goods.goodsId"
                    @click="addOrderList(goods)"
                  >
                    <span class="foodImg"
                      ><img :src="goods.goodsImg" width="100%" height="60px"
                    /></span>
                    <span class="foodName">{{ goods.goodsName }}</span>
                    <span class="foodPrice">￥{{ goods.price }}元</span>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
            <el-tab-pane label="套餐">
              <div
                unselectable="on"
                style="-moz-user-select:none;"
                onselectstart="return false;"
              >
                <ul class="cookList">
                  <li
                    v-for="goods in type3Goods"
                    :key="goods.goodsId"
                    @click="addOrderList(goods)"
                  >
                    <span class="foodImg"
                      ><img :src="goods.goodsImg" width="100%" height="60px"
                    /></span>
                    <span class="foodName">{{ goods.goodsName }}</span>
                    <span class="foodPrice">￥{{ goods.price }}元</span>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
          </el-tabs>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
export default {
  data() {
    return {
      tableData: [],
      oftenGoods: [],
      type0Goods: [],
      type1Goods: [],
      type2Goods: [],
      type3Goods: []
    }
  },
  mounted() {
    var orderHeight = document.body.clientHeight
    document.getElementById('order-list').style.height = orderHeight + 'px'
  },
  created() {
    this.getList()
    this.getType()
  },
  methods: {
    async getList() {
      const res = await this.$http.get(
        'https://www.fastmock.site/mock/0bf6a5bae7eab8507e44b56191ddff36/vuepos/oftenGoods'
      )
      if (res.status !== 200) {
        return this.$message.error('获取数据失败')
      }
      this.oftenGoods = res.data.oftenGoods
      // console.log(this.oftenGoods)
    },
    async getType() {
      const res = await this.$http.get(
        'https://www.fastmock.site/mock/0bf6a5bae7eab8507e44b56191ddff36/vuepos/typeGoods'
      )
      if (res.status !== 200) {
        return this.$message.error('获取数据失败')
      }
      this.type0Goods = res.data.data[0]
      this.type1Goods = res.data.data[1]
      this.type2Goods = res.data.data[2]
      this.type3Goods = res.data.data[3]
    },
    addOrderList(goods) {
      // console.log(goods)
      const index = this.tableData.findIndex(item => {
        return item.goodsId === goods.goodsId
      })
      if (index !== -1) {
        return this.tableData[index].count++
      }
      const obj = {
        goodsId: goods.goodsId,
        goodsName: goods.goodsName,
        count: 1,
        price: goods.price
      }
      this.tableData.push(obj)
      // console.log(this.tableData)
    },
    deleteGoods(goods) {
      const index = this.tableData.findIndex(item => {
        return item.goodsId === goods.goodsId
      })
      this.tableData.splice(index, 1)
    },
    deleteAll() {
      this.tableData = []
    },
    checkOut() {
      if (this.totalNum === 0) {
        return this.$message.error('商品栏为空，不能结账！')
      }
      this.tableData = []
      this.$message.success('结账成功！')
    }
  },
  computed: {
    totalNum() {
      let count = 0
      this.tableData.forEach(item => {
        count += item.count
      })
      return count
    },
    totalPrice() {
      let price = 0
      this.tableData.forEach(item => {
        price += item.count * item.price
      })
      return price
    }
  }
}
</script>

<style lang="less" scoped>
.pos-order {
  background-color: #f9fafc;
  border-right: 1px solid #ccc;
}
.btns {
  margin-top: 10px;
  text-align: center;
}
.title {
  height: 20px;
  border-bottom: 1px solid #d3dce6;
  background-color: #f9fafc;
  padding: 10px;
}
.often-goods {
  height: 50%;
}
.often-goods-list {
  ul {
    li {
      list-style: none;
      float: left;
      border: 1px solid #e5e9f2;
      padding: 10px;
      margin: 10px;
      background-color: #fff;
      cursor: pointer;
    }
  }
}
.o-price {
  color: #58b7ff;
}
.goods-type {
  clear: both;
}
.cookList li {
  list-style: none;
  width: 23%;
  border: 1px solid #e5e9f2;
  height: auot;
  overflow: hidden;
  background-color: #fff;
  padding: 2px;
  float: left;
  margin: 2px;
  cursor: pointer;
}
.cookList li span {
  display: block;
  float: left;
}
.foodImg {
  width: 40%;
}
.foodName {
  font-size: 18px;
  padding-left: 10px;
  color: brown;
}
.foodPrice {
  font-size: 16px;
  padding-left: 10px;
  padding-top: 10px;
}
.total {
  border-bottom: 1px solid #e5e9f2;
  height: 50px;
  line-height: 50px;
  padding: 0 10px;
  display: flex;
  justify-content: center;
  span:nth-child(1) {
    margin-right: 40px;
  }
}
</style>
